import React from "react";
import axios from "axios";

import "./App.css";

import dict from "./dict.json";

const API = "https://otvyoym3th.execute-api.ap-southeast-1.amazonaws.com/dev";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      originText: "",
      translatedText: "",
      audioUrl: undefined
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  async componentDidMount() {
    // await this.handleSubmit();
  }


  handleChange(e) {
    const origin = e.target.value;
    const arrayWord = origin.split(/\s+/g);

    let translateArray = [];
    let translate;

    for (const word of arrayWord) {
      translateArray.push(dict[word.toLowerCase()] || word);
    }
    translate = translateArray.join(" ");

    this.setState({
      originText: origin,
      translatedText: translate
    });
  }

  handleSubmit = async (e) => {
    let response;
    const data = {
      voice: "Joanna",
      text: e.target.value,
    };
    const postOption = {
      method: "POST",
      headers: { "content-type": "application/json" },
      data: JSON.stringify(data),
      url: API
    };
    console.log(postOption)
    try {
      response = await axios(postOption);
      console.log(response);
      setTimeout(() => {
        this.setState({
          audioUrl: `https://s3-ap-southeast-1.amazonaws.com/text-to-speech-english/${response.data}.mp3`
        })
      }, 2000);

    } catch (error) {
      console.log(error);
    }
  }

  render() {
    let audio;
    if (this.state.audioUrl) {
      audio = (
        <audio controls>
          <source src={this.state.audioUrl} type="audio/mpeg" />
        </audio>
      );
    }
    return (
      <center className="container">
        <textarea className="originText" onChange={this.handleChange} />
        <br />
        <textarea
          className="translatedText"
          value={this.state.translatedText}
        />
        <br />
        <button type="submit" onClick={this.handleSubmit} value={this.state.originText}>
          Speak it
        </button>
        <br />
        <h3>Pass your text in the text area, example: My name is Giang</h3>
        <br />
        {audio}
        <br />
      </center>
    );
  }
}

export default App;
